<template>
  <section v-if="!isLoading" class="detail-wrap pt-3">
    <section class="big-mode d-sm-block d-none">
      <div class="d-flex mb-3">
        <section class="w-50">
          <img :src="productUrl" alt class="w-100">
        </section>
        <section class="w-50">
          <div class="d-flex border-bottom pb-3">
            <div>
              <h5 class="text-title">{{productFullTitle}}</h5>
              <h6>: {{productFullId}}</h6>
            </div>
            <div class="ml-auto text-price d-flex mt-6">
              <h5>NT$</h5>
              <span class="big-price">{{productData.price}}</span>
            </div>
          </div>
          <div class="d-flex flex-column mt-3 border-bottom">
            <div class="color_object d-flex mb-3">
              <a
                v-for="(colorItem,index) in productData.colors"
                :key="index"
                class="pt-4 pl-4 mr-3 d-block border bordeer-white"
                :style="{backgroundColor:colorItem.colorNum}"
                @click="colorObjectSelect = colorItem, sizeObjectSelect = colorItem.sizes[0]"
              ></a>
            </div>
            <div class="size_object d-flex mb-3">
              <a
                v-for="(sizeItem,index) in colorObjectSelect.sizes"
                :key="index"
                class="mr-3 py-1 px-6 size-checkbox bg-sizebg"
                @click="sizeObjectSelect = sizeItem"
              >{{sizeItem.sizeName}}</a>
            </div>
            <div class="d-flex ml-auto h6 mb-3">
              <a href class="mr-3"></a>
              <a href class="mr-3"></a>
              <a class href></a>
            </div>
          </div>
          <div class="my-3 d-flex align-items-center">
            <h6 class="text-primary mb-0 mr-1"></h6>
            <div class="d-flex">
              <a class="flex-center border px-1 bg-sizebg" @click="qtyChange(-1)">-</a>
              <input type="text" name id class="qty-input border text-center" v-model.number="qty">
              <a class="flex-center border px-1 bg-sizebg" @click="qtyChange(1)">+</a>
            </div>
            <h6
              @click="addToCart"
              class="add-cart bg-primary text-white rounded px-3 py-2 mb-0 ml-5"
              href
            ></h6>
          </div>
          <h6 class="text-price mb-3"></h6>
        </section>
      </div>
      <section class="text-center">
        <img
          class="w-75 mb-3"
          v-for="ImgNum in productData.introductionImgNum"
          :key="ImgNum"
          :src="introductionImgUrl(ImgNum)"
          alt
        >
        <img
          class="w-75 mb-3"
          v-for="(ImgName,index) in productData.globalImg"
          :key="index"
          :src="globalImgUrl(ImgName)"
          alt
        >
      </section>
    </section>
    <section class="mobile-mode d-block d-sm-none position-relative">
      <div class="w-75 position-relative mx-auto">
        <span class="slick-next slick-arrow position-absolute">&#62;</span>
        <span class="slick-prev slick-arrow position-absolute">&#60;</span>
        <div class="slick-group">
          <img
            v-for="imgNum in productData.introductionImgNum"
            :key="imgNum"
            :src="introductionImgUrl(imgNum)"
            class="w-100"
          >
          <!-- <img 
          v-for="imgNum in productData.introductionImgNum" :src="">-->
        </div>
      </div>
      <div class="d-flex p-3">
        <h5>{{productData.title}}</h5>
        <span class="text-price ml-auto d-flex">
          <h5>$</h5>
          <h3>{{productData.price}}</h3>
        </span>
      </div>
    </section>
  </section>
</template>
<script>
import $ from "jquery";
import slick from "slick-carousel";
import AxiosClass from "@/config/axiosClass.js";
export default {
  name: "detail",
  data() {
    return {
      productData: {},
      colorObjectSelect: {},
      sizeObjectSelect: {},
      qty: 1,
      product_id: ""
    };
  },
  computed: {
    productUrl() {
      const url = `${process.env.VUE_APP_BACKURL}img/${this.product_id}/${
        this.colorObjectSelect.colorId
      }.jpg`;
      return url;
    },
    productFullId() {
      return (
        this.productData.product_id +
        this.colorObjectSelect.colorId +
        this.sizeObjectSelect.sizeId
      );
    },
    productFullTitle() {
      return `${this.productData.title}-${this.productData.gender} (${
        this.colorObjectSelect.colorName
      }-${this.sizeObjectSelect.sizeName})`;
    },
    isLoading() {
      return this.$store.state.isLoading;
    }
  },
  methods: {
    addToCart() {
      let bill = {
        qty: this.qty,
        id: this.product_id,
        colorObject: this.colorObjectSelect,
        sizeObject: this.sizeObjectSelect,
        title: this.productData.title,
        imgUrl: this.productUrl,
        gender: this.productData.gender,
        totalPrice: this.qty * this.productData.price,
        price: this.productData.price,
        fullId: this.productFullId
      };
      this.$store.commit("ADDTOCART", bill);
    },
    qtyChange(num) {
      if (num == -1 && this.qty == 1) {
      } else {
        this.qty += num;
      }
    },
    introductionImgUrl(n) {
      const url = `${process.env.VUE_APP_BACKURL}img/${
        this.product_id
      }/I${n}.jpg`;
      return url;
    },
    globalImgUrl(imgName) {
      const url = `${process.env.VUE_APP_BACKURL}img/global/${imgName}.jpg`;
      return url;
    },
    getProductDetail() {
      const self = this;
      const getProductDetail = new AxiosClass(
        "product_detail/" + this.product_id,
        "get"
      );
      getProductDetail.doAxios(
        self,
        function(res) {
          self.productData = res.data.data;
          self.colorObjectSelect = self.productData.colors[0];
          self.sizeObjectSelect = self.colorObjectSelect.sizes[0];
        },
        function(res) {
          console.log("");
        }
      );
    },
    modileSlickInit() {
      $(".slick-group").slick({
        nextArrow: $(".slick-next"),
        prevArrow: $(".slick-prev")
      });
    },
    initObjectSelect() {}
  },
  created() {
    this.$store.commit("LOADING", true);
    this.product_id = this.$route.params.product_id;
    this.getProductDetail();
  },
  mounted() {
    this.modileSlickInit();
  }
};
</script>
<style lang="scss">
.qty-input {
  width: 30px;
}

.big-price {
  font-size: 70px;
  line-height: 70px;
}
.detail-wrap {
  background-color: #f3f3f3;
  @media (min-width: 575.98px) {
    background-color: white;
  }
}
.add-cart:hover {
  cursor: pointer;
  background-color: brown;
}
.slick-arrow {
  position: absolute;
  font-size: 40px;
  color: white;
  opacity: 0.5;
  top: 50%;
  z-index: 2000;
  transform: translateY(-50%);
  &:hover {
    cursor: pointer;
    opacity: 1;
  }
}
.slick-next {
  right: 0;
}
</style>
