<template>
  <section v-if="!isLoading">
    <img src="@/assets/img/women_banner.jpg" class="w-100 mb-4" alt>
    <div class="row">
      <Card
        class="col-6 col-sm-3"
        v-for="(item,index) in productsData"
        :key="index"
        :cardListProp="item"
      />
    </div>
  </section>
</template>
<script>
import Card from "./cardList_singleCard.vue";
import AxiosClass from "@/config/axiosClass.js";
export default {
  name: "cardList",
  components: {
    Card
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    }
  },
  data() {
    return {
      productsData: []
    };
  },
  methods: {
    testGet() {},
    getProductList: function(gender, category) {
      const self = this;
      const getProductList = new AxiosClass(
        `product/${gender}/${category}`,
        "get"
      );
      getProductList.doAxios(
        self,
        function(res) {
          self.productsData = res.data.data;
          self.$store.commit("LOADING", false);
        },
        function(res) {
          console.log("", res);
          self.$store.commit("LOADING", false);
        }
      );
      /*const self = this;
      axios({
        url: `${process.env.VUE_APP_BACKURL}product/${gender}/${category}`,
        method: "get"
      })
        .then(function(response) {
          self.productsData = response.data.data;
          self.$store.commit("LOADING", false);
        })
        .catch(function(error) {
          console.log(error)
          self.$store.commit("LOADING", false);
        });*/
    }
  },
  created() {
    this.$store.commit("LOADING", true);
    this.getProductList(
      this.$route.params.gender,
      (this.$route.params.category = "all")
    );
  }
};
</script>